@import "a4";
@import "images";
@import "typography";
@import "lists";
@import "table";
@import "layout";

.page-inner {

}


.page-inner {
  //border: solid 1px black;
  margin: 1em;

  flex: 1 0 auto;

  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
}

header {
  border-width: 1px;
  border-color: black;
  border-style: solid;

  padding: 1em;


  display: flex;
  justify-content: space-between;
}

footer {
  border-width: 1px;
  border-color: black;
  border-style: solid;

  padding: 1em;

  display: flex;
  justify-content: space-between;


  .about {
    flex: 0 0 20em;
  }

  .github {


    p {
      margin-top: 1em;
    }
  }

}

#skill-set {
  //Style inner lists
&>div {

  h3 {
    margin: 0.3em 0;
  }

  padding-left: 0.4em;
  display: flex;
  flex-flow: row wrap;

  section {

    flex: 1 0 40%;

    border: none;
    padding: 0;
    margin: 0;


    &:nth-child(odd) {
      flex: 1 0 55%;
    }

    &:nth-child(even) {
      flex: 0 0 40%;
    }

  }
  }

}

#education {

  li {
    list-style-type: none;
    margin: 0.5em 0;
  }
}

#experience {
  li {
    margin: 0.2em;
  }

  h3 {
    margin-top: 0.4em;
  }

  .experience-job-title {
    //font-style: italic;
    margin-left: 0.6em;
    font-weight: bold;
    font-size: 0.9em;
  }

  .experience-time-period {
    font-size: 0.9em;
  }
}



